import React from 'react';
import { Heading as MoHeading } from '@ali/4ever-mo';
import createHeading from './components/createHeading';
import { EditParagraph } from '@ali/4ever-plugin-paragraph';
import { createBiListItem } from '@ali/4ever-plugin-list';

export function createRenderNode() {
  const Paragraph = createBiListItem(EditParagraph);
  const Heading = createHeading(Paragraph);

  return {
    ...MoHeading.TYPES.reduce((acc, type) => {
      return {
        ...acc,
        [type]: (props, _) => {
          const { children } = props;
          return (
            <Heading {...props}>
              {children()}
            </Heading>
          );
        },
      };
    }, {}),
  };
}
